<template>
  <section class="navigation">
    <!-- Modals -->
    <sweet-modal icon="success" ref="successModal" title="Success">
      <span class="success-message">
        Your place has been added successfully
      </span>
    </sweet-modal>

    <sweet-modal icon="error" ref="errorModal" title="Oh noes…">
      There was an error trying adding your place, that's fimr let's try again
      
      <div class="try-again-btn">
        <button @click="tryAgain()">Try again</button>
      </div>
    </sweet-modal>

    <button
      @click="navigatePrevious"
      class="previous-btn"
      :disabled="this.currentStep == 1"
    >
      Previous
    </button>
    <button
      type="submit"
      class="next-btn"
      @click="navigateNext"
      v-if="this.currentStep !== this.totalSteps"
      :disabled="!valid"
    >
      Next
    </button>
    <button
      type="submit"
      class="finsih-btn"
      v-if="this.currentStep == this.totalSteps"
      :disabled="!valid"
      @click="finishForm"
    >
      Finish
    </button>
  </section>
</template>

<script>
import { SweetModal, SweetModalTab } from "sweet-modal-vue";

export default {
  components: { SweetModal, SweetModalTab },
  props: {
    valid: {
      type: Boolean,
    },
  },
  computed: {
    currentStep: {
      get() {
        return this.$store.state.currentStep;
      },
      set(value) {},
    },
    stepLength: {
      get() {
        var stpLng = (1 / Object.keys(this.$store.state.steps).length) * 100;
        return stpLng;
      },
    },
    totalSteps: {
      get() {
        var total = Object.keys(this.$store.state.steps).length;
        return total;
      },
    },
  },
  methods: {
    navigateNext() {
      if (this.currentStep <= this.totalSteps - 1) {
        this.$store.commit("updateCurrentStep", {
          currentStep: this.currentStep + 1,
        });
        this.setProgress();
        this.$router.push(`/add/${this.currentStep}`);
      }
    },
    navigatePrevious() {
      if (this.currentStep !== 0) {
        this.$store.commit("updateCurrentStep", {
          currentStep: this.currentStep - 1,
        });
        this.setProgress();
        this.setProgress();
        this.$router.push(`/add/${this.currentStep}`);
      }
    },
    resetProgress() {
      this.$store.commit("updateCurrentStep", {
        currentStep: 1,
      });
      this.setProgress();
    },
    setProgress() {
      this.$store.commit("setProgress", {
        progressNow: this.stepLength * (this.currentStep - 1),
      });
    },
    finishForm() {
      this.$store.commit("setProgress", {
        progressNow: "100",
      });
      this.showSuccessModal();
    },
    tryAgain() {
      this.closeSuccessModal();
      this.resetProgress();

      this.$router.push("/add/1");
    },

    // Modals
    showSuccessModal() {
      this.$refs.successModal.open();
    },
    closeSuccessModal() {
      this.$refs.successModal.close();
    },
    showErrorModal() {
      this.$refs.errorModal.open();
    },
    closeErrorModal() {
      this.$refs.errorModal.close();
    },
  },
  created() {},
};
</script>

<style scoped lang="scss">
.navigation {
  margin-top: 1rem;
}
.success-message {
  font-size: 20px;
  font-weight: bold;
}
.try-again-btn {
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
